@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --primary-color: #F27B35;
    --secondary-color: #222831;
    --text-color: #ffffff;
    --background-color: #222831;
    --border-color: rgba(238, 238, 238, 0.1);
    --hover-color: #ff7f50;
    --button-background-color: rgba(57, 62, 70, 0.75);
    --button-hover-color: #555;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #222831;
  color: white;
  font-size: 16px;
}

section {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-top: 45px;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  margin: 0 60px 0;
}

.app-header {
  width: 100%;
  padding: 25px 0;
  position: relative;
  margin-bottom: 5rem;
}

.app-header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background-color: rgba(238, 238, 238, 0.1);
  margin-left: -60px;
  margin-right: -60px;
}

.header-content {
  max-width: 1400px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.logo {
  font-size: 1.25rem;
  font-weight: bold;
}

.nav {
  display: flex;
  align-items: center;
}

.nav a {
  margin: 0 15px;
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

.nav a:hover {
  color: #ff7f50;
}

.menu-icon {
  display: none;
  font-size: 1.5rem;
  cursor: pointer;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 1;
  width: 100%;
  position: relative;
  grid-gap: 8rem;
  gap: 8rem;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 998;
}

.close-menu {
  display: none;
}

.close-menu {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
}

.highlight {
  color: var(--primary-color);
}

@media (max-width: 768px) and (orientation: portrait) {
  body {
    font-size: 14px;
  }

  .app {
    margin: 0 20px;
  }

  .app-header {
    margin-bottom: 2.5rem;
  }

  .nav {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    z-index: 999;
  }

  .nav.open {
    display: flex;
  }

  .menu-icon {
    display: block;
  }

  .close-menu {
    display: block;
  }

  .logo {
    font-size: 1rem;
  }

  .nav a {
    font-size: 0.875rem;
    margin: 10px 0;
  }

  .main {
    grid-gap: 4rem;
    gap: 4rem;
  }
}

@media (max-width: 768px) and (orientation: landscape) {
  body {
    font-size: 14px;
  }

  .logo {
    font-size: 0.9rem;
  }

  .nav a {
    font-size: 0.9rem;
  }
}

@media (min-width: 1200px) {
  body {
    font-size: 18px;
  }

  .logo {
    font-size: 1.2rem;
  }

  .nav a {
    font-size: 1rem;
  }
}
.home-section .content {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.home-section .text-column {
    flex: 1 1;
    text-align: left;
    position: absolute;
}

.home-section .title {
    font-size: 2.5rem;
    font-weight: bold;
    margin: 0;
}

.home-section .subtitle {
    color: #F27B35;
    font-size: 1.4rem;
    font-weight: bold;
    margin: 0;
}

.home-section .buttons {
    margin-top: 60px;
}

.home-section .image-column {
    flex: 1 1;
    display: flex;
    justify-content: right;
    align-items: flex-start;
    margin-right: -50px;
}

.home-section .image-column img {
    width: 100%;
    max-width: 300px;
    height: auto;
    object-fit: cover;
}

@media (max-width: 768px) and (orientation: portrait) {
    .home-section .content {
        flex-direction: column;
        align-items: center;
    }

    .home-section .text-column {
        position: static;
        text-align: center;
        margin-bottom: 20px;
    }

    .home-section .image-column {
        margin-top: 0;
        margin-right: 0;
        justify-content: center;
    }

    .home-section .image-column img {
        width: 100%;
        max-width: 300px;
        height: auto;
    }

    .home-section .title {
        font-size: 2.5rem;
    }

    .home-section .subtitle {
        font-size: 1.25rem;
    }

    .home-section .buttons {
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-top: 20px;
    }

    .home-section .btn {
        margin: 10px;
    }
}

@media (max-width: 768px) and (orientation: landscape) {
    .home-section .title {
        font-size: 2.4rem;
    }

    .home-section .subtitle {
        font-size: 1.1rem;
    }

    .home-section .content {
        width: 100%;
    }

    .home-section .image-column {
        margin-right: -50px;
        margin-top: -10px;
    }

    .home-section .image-column img {
        height: 80vh;
        width: auto;
    }
}

@media (min-width: 1200px) {
    .home-section .title {
        font-size: 3.8rem;
    }

    .home-section .subtitle {
        font-size: 1.9rem;
    }

    .home-section .image-column {
        margin-right: 0px;
        margin-top: -10px;
    }

    .home-section .image-column img {
        max-width: 650px;
        height: 70vh;
        width: auto;
    }
}
.my-expertise-section .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  max-width: 1200px;
}

.my-expertise-section .title {
  font-size: 3.5rem;
  font-weight: bold;
  margin: 0;
}

.my-expertise-section .boxes {
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
  margin-top: 70px;
  grid-gap: 20px;
  gap: 20px;
  width: 100%;
}

.my-expertise-section .box {
  color: white;
  padding: 40px;
  flex: 1 1 300px;
  max-width: 60%;
  box-sizing: border-box;
  text-align: left;
  border: 2px solid #fff;
  display: flex;
  flex-direction: column;
}

.my-expertise-section .box .header {
  display: flex;
  align-items: center;
  justify-content: start;
  grid-gap: 30px;
  gap: 30px;
  margin: 0;
  padding: 0;
}

.my-expertise-section .box .header .icon {
  font-size: 2.6rem;
}

.my-expertise-section .box h3 {
  margin: 0;
  font-size: 1.4rem;
  color: #fff;
}

.line-with-text {
  display: flex;
  align-items: center;
  margin-top: 30px;
}

.my-expertise-section .box p {
  padding: 10px 0;
  font-size: 1rem;
  line-height: 1.5rem;
}

.my-expertise-section .highlight {
  color: #F27B35;
}

.my-expertise-section .icon {
  font-size: 2rem;
}

.my-expertise-section .image-container {
  margin-top: 40px;
  width: 80%;
  display: flex;
  justify-content: center;
  top: -70px;
  position: relative;
}

.my-expertise-section .background-image {
  max-width: 80%;
  height: auto;
}

@media (max-width: 768px) {
  .my-expertise-section .title {
    font-size: 2.5rem;
  }

  .my-expertise-section .box {
    flex: 1 1 100%;
    max-width: 100%;
    padding: 25px;
  }

  .my-expertise-section .box h3 {
    font-size: 1.2rem;
  }  

  .my-expertise-section .box .header .icon {
    font-size: 2rem;
  }

  .line-with-text {
    margin-top: 15px;
  }
}

@media (max-width: 480px) {
  .my-expertise-section .title {
    font-size: 2rem;
  }

  .my-expertise-section .box .header .icon {
    font-size: 1.6rem;
  }
}
.my-work-section .content {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.my-work-section .title {
    font-size: 3.5rem;
    font-weight: bold;
    margin: 0;
    margin-bottom: 20px;
}

.my-work-section .btn {
    padding: 9px 30px;
    margin-right: 10px;
    border: none;
    border-radius: 24px;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}

.my-work-section .btn.primary {
    background-color: var(--primary-color);
    color: white;
}

.my-work-section .btn.primary:hover {
    background-color: var(--primary-color);
}

.my-work-section .btn.secondary {
    background-color: var(--button-background-color);
    color: white;
}

.my-work-section .btn.secondary:hover {
    background-color: var(--button-hover-color);
}

.my-work-section .work-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 50px;
    gap: 50px;
    margin-top: 60px;
}

.my-work-section .pagination {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 40px 0;
}

.my-work-section .pagination span {
    margin: 0 20px;
    font-family: 'Poppins', sans-serif;
    font-size: 1rem;
}

.my-work-section .arrow {
    cursor: pointer;
}

@media (max-width: 768px) {
    .my-work-section .title {
        font-size: 2.5rem;
    }

    .my-work-section .work-list {
        grid-template-columns: 1fr;
        grid-gap: 20px;
        gap: 20px;
    }
}

@media (max-width: 480px) {
    .my-work-section .title {
        font-size: 2rem;
    }
}
.footer {
    background-color: #222831;
    color: white;
    padding: 20px 0;
    text-align: center;
}

.footer-content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-nav {
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 20px;
}

.footer-nav a {
    color: white;
    text-decoration: none;
    font-size: 1rem;
}

.footer-nav a i {
    margin-right: 8px;
}

.social-icons {
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 20px;
}

.social-icons a {
    color: white;
    font-size: 1.5rem;
    text-decoration: none;
}

.footer-legal {
    font-size: 0.875rem;
}

.footer-legal p {
    color: white;
    text-decoration: none;
}
.about-me-section .content {
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    grid-gap: 20px;
    gap: 20px;
}

.about-me-section .text-column {
    flex: 1 1;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.about-me-section .title {
    font-size: 3.5rem;
    font-weight: bold;
    margin: 0;
    margin-bottom: 20px;
}

.about-me-section .text {
    font-size: 1rem;
}

.about-me-section .image-column {
    flex: 2 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-me-section .image-column img {
    width: 85%;
    height: auto;
    object-fit: cover;
}

@media (max-width: 768px) {
    .about-me-section .content {
        flex-direction: column;
        grid-gap: 40px;
        gap: 40px;
    }

    .about-me-section .title {
        font-size: 2.5rem;
    }
}

@media (max-width: 480px) {
    .about-me-section .title {
        font-size: 2rem;
    }
}
